.ScreenCast {
  width: 100%;
  height: 100%;
  image-rendering: pixelated; /* Disables interpolation. See https://stackoverflow.com/questions/7615009/disable-interpolation-when-scaling-a-canvas */
}

.imageHidden {
  display: none;
}
