@import '@carbon/type/scss/styles';
@import '@carbon/type/scss/font-family';

.TracingInfo {
  :global .cds--grid {
    padding-left: 0;
    padding-right: 0;
  }

  .hidden {
    display: none;
  }

  .formSectionAppendTitle {
    margin-top: 2rem;
  }

  .formSectionLevelTitle {
    margin-top: 2rem;
    margin-bottom: 0.75rem;
  }

  .selectAllAndCountContainer {
    display: flex;
    align-items: center;
  }

  .selectAllChild {
    flex: 1;
    margin-right: 0rem;
  }

  .countChild {
    @include carbon--type-style('label-01');
    flex: 4;
  }

  :global .cds--list__item::before {
    display: none;
  }

  :global .cds--toggle {
    margin-top: 1rem;
  }
}
