.HostVariables {
  display: flex;
  flex-direction: row;
  margin-bottom: 8rem;
  margin-left: -3em;
  margin-right: -3em;

  :global input:disabled {
    outline: 2px solid transparent;
    outline-offset: -2px;
    color: #525252;
    background-color: #262626;
    border-bottom: 1px solid transparent;
    -webkit-text-fill-color: currentColor;
    cursor: not-allowed;
  }

  :global input::placeholder {
    color: #6f6f6f;
  }

  :global input:disabled::placeholder {
    color: #525252;
  }

  :global .cds--data-table-header {
    padding-right: 1.5rem;
  }

  :global .cds--data-table-content {
    overflow-x: unset;
  }

  .tableDescriptionSection {
    display: inline-block;
    margin-bottom: 1rem;
  }

  .tableDescriptionSection:last-child {
    margin-bottom: 0;
  }

  .tableColumn:nth-child(2) {
    width: 20%;
    min-width: 10rem;
  }

  .tableColumn:nth-child(4) {
    width: 70%;
    min-width: 20rem;
  }
}
