.LoadingBar {
  pointer-events: none;
  transition: 400ms linear all;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.Bar {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  display: none;
  width: 100%;
  height: 2px;
  background: #29d;
  border-radius: 0 1px 1px 0;
  transition: width 350ms;
}

.Peg {
  position: absolute;
  top: 0;
  right: 0;
  width: 70px;
  height: 2px;
  border-radius: 50%;
  opacity: 0.45;
  box-shadow: #29d 1px 0 6px 1px;
}

.Spinner {
  position: fixed;
  top: 5px;
  left: 5px;
  z-index: 1000;
  pointer-events: none;
  transition: 350ms linear all;
}
