// Import Carbon v11 theme
@use '@carbon/react/scss/themes' as themeVars;
@use '@carbon/react/scss/theme' as themes;

// Import all Carbon v11 styles
@use '@carbon/react';

:root:has(body.vscode-light) {
  @include themes.theme(themeVars.$white);
}

:root:has(body.vscode-dark) {
  @include themes.theme(themeVars.$g100);
}

:root:has(body.vscode-high-contrast) {
  @include themes.theme(themeVars.$g100);
}


// Import Carbon v10 theme
// TODO: Remove when Carbon v10 components are no longer used.
// Carbon v11 doesn't fully support TypeScript currently, so some v10 components
// from carbon-components-react are imported.
@import '@carbon/themes/scss/themes';

$feature-flags: (
  enable-css-custom-properties: true,
);

body.vscode-light {
    @include carbon--theme($carbon--theme--white, true);
}

body.vscode-dark {
    @include carbon--theme($carbon--theme--g100, true);
}

body.vscode-high-contrast {
    @include carbon--theme($carbon--theme--g100, true);
}


// Import all Carbon v10 styles
// TODO: optimize sass build - https://github.com/carbon-design-system/carbon/blob/main/docs/guides/sass.md
@import 'carbon-components/scss/globals/scss/styles.scss';
