 .TuningServicesAccessPathAdvisor {
    .AccordionItemOverride {
        padding-right: inherit;
    }

    .StructuredListWrapperOverride {
        border-collapse: inherit;
        margin-bottom: 0;
        width: calc(100% + 25% + 3rem);

        .StructuredListCellOverride {
            padding-left: inherit;
            padding-right: inherit;
        }
    }

    .Recommendation {
        display: flex;
        width: 100%;
    }

    .RecommendationBuffer {
        flex-grow: 1;
    }

    .RecommendationNumber {
        width: 3%
    }

    .RecommendationSeverity {
        width: 10%;
    }

    .RecommendationContent {
        white-space: pre-wrap;
        width: 82%;
    }
}
