.ExecutionConfig {
  .formGroups {
    display: flex;
    flex-direction: row;
    justify-content: left;
  }

  .radios {
    display: flex;
    flex-direction: column;
  }

  .twoOptions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .firstOption {
    margin: 16px;
  }

  .tileWrap {
    margin: 8px;
    padding: 8px;
  }

  .verticalBar {
    width: 2px;
    background-color: #d4d4d4;
  }

  .checkboxContainer {
    margin-bottom: 80px;
  }
}
