@import '@carbon/layout/scss/layout';

.FilterPopOver {
  .boxPadding {
    padding: $spacing-04;
  }

  .filterColumn {
    display: flex;
    align-items: flex-end;
    margin-top: $spacing-05;
  }

  .columnRow {
    display: flex;
    width: 86%;
  }

  .buttonRow {
    display: flex;
    width: 14%;
  }

  .filterRadioButton {
    margin-top: $spacing-05;
  }

  .filterCancel {
    margin-right: $spacing-03;
  }

  .filterButton {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: $spacing-04;
  }

  .selectBox {
    padding-right: $spacing-04;
    width: $spacing-13;
  }

  .popoverContent {
    min-width: 42rem;
  }

  :global .bx--list-box .bx--select-input__wrapper {
    width: $spacing-13;
  }

  :global .cds--date-picker.cds--date-picker--single .cds--date-picker__input {
    width: $spacing-13;
  }
}
