@import '@carbon/colors/scss/colors';
.DeploymentTab {
  margin-bottom: 8rem;

  .connectionSelect {
    margin-top: 1.5em;
    margin-bottom: 2em;
  }

  .deploymentTitle {
    padding-top: 1em;
    padding-bottom: 1em;
  }

  .nowrapText {
    padding-top: 0.5em;
    white-space: nowrap;
  }

  .textInputstyle {
    margin-top: 2em;
    margin-bottom: 2em;
  }

  .detailInfoStyle {
    margin-top: 1.5em;
    margin-bottom: 0.5em;
  }

  .radioContainer {
    display: flex;
    flex-direction: row;
    margin-bottom: 0.5rem;
    margin-top: 0.5rem;
  }

  .radioPlaceHolder {
    margin-top: 0.5rem;
  }

  :global .cds--select {
    width: 20rem;
  }

  :global .cds--text-input {
    width: 20rem;
    min-width: 8rem;
    max-width: 28rem;
  }
}
