@import '@carbon/colors/scss/colors';
@import '@carbon/layout/scss/layout';

.ExpandTable {

  height: 20rem;
  overflow-y:auto;

  .container {
    padding-left: 0;
    padding-right: 0;
  }

  .formSectionTitleFirst {
    margin-top: 0;
    margin-bottom: $spacing-06;
  }

  .expandControl {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .multiSelectBox {
    margin-bottom: 0.9rem;
  }

  .runtimeDependencies {
    padding-top: $spacing-07;
    padding-right: $spacing-05;
  }

  .uploadWrap {
    padding-bottom: $spacing-05;
  }

  .inputWrap {
    display: flex;
    flex-direction: row;
  }

  :global .cds--table-sort__flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  :global .cds--grid {
    padding-left: 0;
    padding-right: 0;
  }

  :global .cds--text-input{
    min-width: 21rem;
  }

  .invalidTextWrapper {
    max-width: 21rem;
  }
}
